/************************************
 * CSS-Ballon
************************************/
.balloon {
    display: inline-block;
    position: relative;
    background: #ccc;
    height: 50px;
    width: 120px;
    margin: 0 auto 10px;
}
.balloon:after {
    content: '';
    position: absolute;
    border-top: 10px solid #ccc;
    border-right: 5px solid transparent;
    border-left: 5px solid transparent;
    bottom: -9px;
    left: 5px;
}
/************************************
 * CSS-preset-img
************************************/
.imgRanderer{
	background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}
/************************************
 * CSS-preset-custom-w3-card
************************************/
.custom-w3-card{
    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.05), 0 2px 10px 0 rgba(0,0,0,0.05);
}
/************************************
 * CSS-preset-box_Shadow
************************************/
.boxShadow{
	box-shadow         : 5px 15px 83px 32px rgba(0,0,0,0.74);
}
.boxShadow-reverse{
	box-shadow         : 0px 10px 42px 27px rgba(255,255,255,0.9);
}
.boxShadow-lite{
	box-shadow         : -1px 22px 41px 7px rgba(0,0,0,0.19);
}
.boxShadow-reverse-lite{
	box-shadow: -1px 22px 41px 7px rgba(255,255,255,1);
}
/************************************
 * CSS-preset-button
************************************/
.btnMargin{
	margin-right: 10px;
}
.btnBorderBottom{
	border-bottom: 1px solid #cbcbcb;
}
/************************************
 * CSS-preset-ETC
************************************/
.popUp{
	position: fixed;
	display : none;
	z-index : 100;
	top     : 40%;
}
.errMsg{
	display : none;
}
p{
	margin: 0px;
	padding: 0px;
}
/************************************
 * CSS-ANIME
************************************/
@-webkit-keyframes fader {
		  from {
		    opacity: 0;
		  }
		  to {
		    opacity: 1;
		  }
		}
@keyframes fader {
		  from {
		    opacity: 0;
		  }
		  to {
		    opacity: 1;
		  }
		}
.fader {
  -webkit-animation: fader 0.3s linear ;
          animation: fader 0.3s linear ;
}
