@media only screen and ( min-width : 0px ){
	/************************************
	 * 0px---> POST-STYLE
	************************************/
	.post-body{
	    margin: 30px 20px 30px 20px;
	}
	.post-img-area{
		overflow:hidden;
	}
	
	/************************************
	 * 0px---> POST-STYLE-textarea
	************************************/
	.post-header{
		font-weight: bold;
	}
	.post-header > P::first-letter{
		font-size: 150%;
	}
	/************************************
	 * 0px---> POST-STYLE-imgArea
	************************************/
	
	.post-img-area{
		margin: 0 auto;
		width : 460px;
		height: 230px;
	}
	.cl_img_post_thumbnail{
		max-width: 460px;
	}
	/************************************
	 * 0px---> MAIN-CONTENT
	************************************/
	.post-footer-text{
	    top: 0px;
	}
	/************************************
	 * 0px---> SIDEBAR
	************************************/
	.main-sidebar{
		width: 45%;
		z-index: 100;
	}
	/************************************
	 * 0px---> SIDEBAR
	************************************/
	.post_viewImg{
		width: 100%;
	}
	/* div.viewPostHeader{
		width: 100%;
	} */
	/************************************
	************************************/
	
	
}
@media only screen and ( min-width : 769px ){
	/************************************
	 * 769px---> POST-STYLE
	************************************/
	.post-body{
	    margin: 30px 20px 30px 20px;
	}
	.post-content::after{
		clear:both;
		display : block;
		content: '';
	}
	/************************************
	 * 769px---> POST-STYLE-textarea
	************************************/
	.post-header{
		font-weight: bold;
	}
	.post-text-area{
		width: 527px;
		float: left;
	}
	.post-header > P::first-letter{
		font-size: 150%;
	}
	/************************************
	 * 769px---> POST-STYLE-imgarea
	************************************/
	.post-img-area{
		float: right;
	}
	.post-img-area{
		width : 167px;
		height: 167px;
	}
	.cl_img_post_thumbnail{
		max-height: 167px;
	}
	/************************************
	 * 769px---> MAIN-CONTENT
	************************************/
	.cl_div_mainContent, div.viewPostArticle{
		width: 100%;
	}
	.post-footer-text{
	    top: -25px;
	}
	/************************************
	 * 769px---> SIDEBAR
	************************************/
	.main-sidebar{
		width: 20%;
	}
	/************************************
	************************************/
}
@media only screen and ( min-width : 1024px ){
		
	.cl_div_mainContent, div.viewPostArticle{
		width: 1024px;
		margin: 0 auto;
	}
	
	/************************************
	************************************/
}
